.sign-page {
    margin: 0;
    padding: 110px 0;
    width: 100%;
    min-height: 100vh;
    box-sizing: border-box;
    background-color: var(--colors_bg_main);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.sign-page__wrapper {
    margin: 0;
    padding: 0;
    width: 396px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
}

.sign-page__title {
    margin: 0;
    padding: 0;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
}

@media screen and (max-width: 767px) {
    .sign-page__wrapper {
        width: 260px;
    }
}