.avatar {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    aspect-ratio: 1/1;
    border-radius: 50%;
    overflow: hidden;
    background-position: center;
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

.avatar__image {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    object-position: center;
    object-fit: cover;
    aspect-ratio: 1/1;
    display: none;
}

.avatar_place_actor-card {
    width: 100px;
    min-width: 100px;
}

.avatar_place_review {
    width: 100%;
}

.avatar_place_header {
    width: 45px;
}

@media screen and (max-width: 1023px) {
    .avatar_place_actor-card {
        width: 85px;
        height: 85px;
        min-width: 85px;
    }
}

@media screen and (max-width: 767px) {
    .avatar_place_actor-card {
        width: 75px;
        height: 75px;
        min-width: 75px;
    }
}