.rating-popup {
    margin: 0;
    padding: 40px;
    box-sizing: border-box;
    position: relative;
    border-radius: 10px;
    background-color: var(--colors_bg_main);
    box-shadow: 0px 0px 30px rgba(255, 255, 255, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
}

.rating-popup__title {
    margin: 0;
    padding: 0;
    color: var(--fonts_colors_main);
    font-size: 30px;
    font-weight: 700;
}

.rating-popup__buttons {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    list-style: none;
}

.rating-popup__button {
    margin: 0;
    padding: 0 5px;
    box-sizing: border-box;
}

@media screen and (max-width: 1023px) {
    .rating-popup__title {
        font-size: 25px;
    }
}

@media screen and (max-width: 767px) {
    .rating-popup {
        padding: 30px 20px;
        gap: 40px;
    }

    .rating-popup__title {
        font-size: 18px;
        font-weight: 600;
    }
}