.type-selector {
    margin: 0;
    padding: 3px;
    box-sizing: border-box;
    background-color: var(--colors_checkbox_bg);
    border-radius: 17.5px;
    display: flex;
    box-shadow: 0 0 1px 0 rgba(247, 248, 255, 0.1), 0 6px 12px 0 rgba(247, 248, 255, 0.1);
    backdrop-filter: blur(10px);
    position: relative;
}

.type-selector__input {
    display: none;
}

.type-selector__label {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    width: 80px;
    height: 24px;
    text-align: center;
    color: var(--fonts_colors_main);
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    vertical-align: middle;
    position: relative;
    z-index: 1;
    transition: color .2s ease;
}

.type-selector__input:checked + .type-selector__label {
    color: var(--colors_btn_crimson_hover);
    transition: color .2s ease;
}

.type-selector__label:hover {
    cursor: pointer;
}

.type-selector__slider {
    margin: 0;
    padding: 0;
    display: block;
    width: 80px;
    height: 24px;
    background-color: var(--colors_checkbox_slider);
    border-radius: 15px;
    position: absolute;
    transform: translateX(0);
    transition: transform .2s ease;
}

.type-selector__input:last-of-type:checked ~ .type-selector__slider {
    transform: translateX(100%);
    transition: transform .2s ease;
}