.slide-main {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100vh;
    position: relative;
}

.slide-main::after {
    content: '';
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background:
        linear-gradient(45deg, transparent 95%, var(--colors_bg_main)),
        linear-gradient(315deg, transparent 95%, var(--colors_bg_main)),
        linear-gradient(135deg, transparent 92.5%, var(--colors_bg_main)),
        linear-gradient(225deg, transparent 92.5%, var(--colors_bg_main)),
        linear-gradient(90deg, transparent 70%, var(--colors_bg_main)),
        linear-gradient(270deg, transparent 70%, var(--colors_bg_main)),
        linear-gradient(to bottom, transparent 92.5%, var(--colors_bg_main)),
        linear-gradient(to right, rgb(2, 3, 19, .3) 50%, transparent);
}

.slide-main__content-wrapper {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    display: block;
    max-width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 20px;
    position: absolute;
    top: 50%;
    left: 150px;
    transform: translateY(-50%);
    z-index: 1;
}

.slide-main__title {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: var(--fonts_colors_btn);
    font-weight: 700;
    font-size: 48px;
}

.slide-main__info {
    margin: 0;
    padding: 0 20px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    gap: 30px;
    border-left: 2px solid var(--fonts_colors_fade);
}

.slide-main__release-date {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: var(--fonts_colors_main);
    font-weight: 500;
    font-size: 20px;
}

.slide-main__description {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: var(--fonts_colors_main);
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    opacity: .9;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    line-clamp: 4;
    -webkit-box-orient: vertical;
}

@media screen and (max-width: 1023px) {
    .slide-main__content-wrapper {
        gap: 25px;
        left: 90px;
    }

    .slide-main__title {
        font-size: 35px;
    }

    .slide-main__description {
        font-size: 18px;
        line-height: 22px;
    }
}

@media screen and (max-width: 767px) {
    .slide-main__content-wrapper {
        padding: 0 30px;
        max-width: 100%;
        align-items: center;
        gap: 30px;
        left: 0;
    }

    .slide-main__title {
        font-size: 30px;
        text-align: center;
    }

    .slide-main__description {
        text-align: center;
        font-size: 16px;
        line-height: 18px;
        -webkit-line-clamp: 6;
        line-clamp: 6;
    }
}